import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { createProject } from '../../store/actions/projectActions'
import Footer from './footer'

export class SignUp extends Component {
    state = {
        email: '',
        password: '',
        fullName: ''
    }
    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }
    handleSubmit = (e) => {
        e.preventDefault();
        // console.log(this.state);
        this.props.createProject(this.state)
    }
    render() {
        return (
            <div>
                {/* < !--Form starts here-- > */}
                <section class="bk-grey">
                    <div class="container">
                        <div class="row">
                            <div class="col-md-7 login-form">
                                <form onSubmit={this.handleSubmit}>
                                    <div class="main-text">
                                        <h2><i class="fa fa-user"></i> Sign Up</h2>
                                    </div>
                                    <div class="signup-cat row">
                                        <div class="col-md-6">
                                            <button class="btn btn-candidate">Candidate</button>
                                        </div>
                                        <div class="col-md-6">
                                            <button class="btn btn-candidate">Employee</button>
                                        </div>
                                    </div>
                                    <div class="form-group">
                                        <label htmlFor="exampleInputEmail1">Username</label>
                                        <input type="text" class="form-control" id="fullName" required
                                            onChange={this.handleChange} aria-describedby="emailHelp" placeholder="Username" />
                                    </div>
                                    <div class="form-group">
                                        <label htmlFor="exampleInputEmail1">Email address</label>
                                        <input type="email" class="form-control" id="email" required
                                            onChange={this.handleChange} aria-describedby="emailHelp" placeholder="Enter email" />
                                    </div>
                                    <div class="form-group">
                                        <label htmlFor="exampleInputPassword1">Password</label>
                                        <input type="password" class="form-control" id="password" required
                                            onChange={this.handleChange} placeholder="Password" />
                                    </div>
                                    <div class="row">
                                        <div class="form-group form-check check-login col-md-6">
                                            <input type="checkbox" class="form-check-input" id="exampleCheck1" />
                                            <label class="form-check-label" for="exampleCheck1">Remember me</label>
                                        </div>
                                        <div class="col-md-6 text-right login-txt">
                                            <Link to="">
                                                Forget Password
                                </Link>

                                        </div>
                                    </div>
                                    <div class="login">
                                        <button type="submit" class="btn btn-login btn-block">Sign Up</button>
                                    </div>
                                    <div class="connect">
                                        <h2>Or connect with</h2>
                                    </div>
                                    <div class="row fg-buttons">
                                        <div class="col-md-6">
                                            <Link to="" class="btn btn-facebook"><i class="fa fa-facebook"></i> Facebook</Link>
                                        </div>
                                        <div class="col-md-6">
                                            <Link to="" class="btn btn-google"><i class="fa fa-google"></i>Google</Link>
                                        </div>
                                    </div>
                                    <div class="connect">
                                        <h2><Link to="/sign-in"> You already have an account? Sign In</Link></h2>
                                    </div>
                                </form>
                            </div>
                            <div class="col-md-5 login-img">
                                <img src="../images/vector1.svg" class="img-responsive" alt="" />
                            </div>
                        </div>
                    </div>
                </section>
                <Footer />

                {/* <!-- Form ends here --> */}
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        createProject: (project) => dispatch(createProject(project))
    }
}

export default connect(null, mapDispatchToProps)(SignUp)



